import React, { useEffect } from 'react';
import { NavLink } from 'react-router-dom';
import MainContent from './Main';
import { logout } from '../../actions/auth';
import { connect } from 'react-redux';
import ME from '../../ME.png';
import DepressiveChart from './Chart';


const SidebarItem = ({ name }) => (
  <NavLink
    to={`/${name.toLowerCase()}`}
    className="block mb-4 px-4 py-2 rounded-md text-gray-300 hover:bg-indigo-700 hover:text-white"
    activeClassName="bg-indigo-700 text-white"
  >
    {name}
  </NavLink>
);

const Profile = ({ name }) => (
  <div className="p-4 flex flex-col items-center">
    <img
      src={ME}
      alt="Profile"
      className="h-32 w-32 rounded-full mb-4 object-center"
    />
    <h3 className="text-2xl font-bold mb-2 text-white">{name}</h3>
    <p className="text-lg font-medium mb-4 text-gray-300">
      
    </p>
    <div className="flex justify-around mb-4">
      <NavLink
        to="/profile"
        className="text-indigo-300 font-medium hover:text-indigo-100"
      >
        View profile
      </NavLink>
    </div>
  </div>
);


const Sidebar = ({ name }) => (
  <aside className="bg-indigo-800 h-full p-4 rounded-lg position-sticky top-0">
    <Profile name={ name }/>
    <div className="mb-8" /> {/* extra space between the profile and the first sidebar item */}
    <SidebarItem name="Statistics" />
    <div className="mb-8" /> {/* extra space between sidebar items */}
    <SidebarItem name="Weekly report" />
    <div className="mb-8" />
    <SidebarItem name="Talk to an expert" />
    <div className="mb-8" />
    <SidebarItem name="Connect with others" />
    <div className="mb-8" />
    <SidebarItem name="Community" />
    <div className="mb-8" />
    <SidebarItem name="Tips & advice" />
  </aside>
);


const Header = ({ logout }) => (
  <header className="bg-white flex px-6 py-4 shadow-md z-10">
    <a href='/'><h3 className="text-2xl font-bold text-gray-900">Dashboard</h3></a>
    <div className="ml-auto">
      <button onClick={logout} className="px-4 py-2 rounded-md bg-white text-indigo-700 font-medium hover:bg-indigo-700 hover:text-white">
        Logout
      </button>
    </div>
  </header>
);


const Dash = ({ data, name, logout }) => {
  useEffect(() => {
    // data has now been fully loaded
  }, [data]);
  return(
  <div className="bg-gray-100 h-screen flex flex-col">
    <Header logout={logout}/>
    <main className="flex-1 overflow-y-auto px-6 py-4 relative">
      <div className="flex h-full">
        <Sidebar name={name}/>
        {data ? (
          <div className="flex flex-col flex-1 items-center">
          <MainContent total={data.total} depressive={(((data.depressive)*100)/data.total).toFixed(2)} hours={(data.time).toFixed(2)} />
          <DepressiveChart data={data} />
          </div>
          
        ) : null}
      </div>
    </main>
  </div>
);}

export default connect(null, { logout })(Dash);
