import React, { useEffect, useRef } from 'react';
import Chart from 'chart.js/auto';
import { useSelector, useDispatch } from 'react-redux';
import { get_user_data } from '../../actions/auth';
import { useNavigate } from 'react-router-dom';

const Statistics = () => {
  const DepressiveChartRef = useRef(null);
  const SuicidalChartRef = useRef(null);
  const CyberbullyingChartRef = useRef(null);
  const dispatch = useDispatch();
  const navigate = useNavigate();

  const chartOptions = {
    responsive: true,
    scales: {
      y: {
        beginAtZero: true,
      },
    },
  };

  const isAuthenticated = useSelector((state) => state.auth.isAuthenticated);
  const user = useSelector((state) => state.auth.user);

  useEffect(() => {
    const fetchData = async () => {
      const data = await dispatch(get_user_data(user.email));
  
        const DepressiveChart = new Chart(DepressiveChartRef.current, {
          type: 'line',
          data: {
            labels: Array.from({ length: 24 }, (_, index) => `${index}:00`),
            datasets: [
              {
                label: 'Depressive',
                data: [],
                backgroundColor: 'rgba(75, 192, 192, 0.2)',
                borderColor: 'rgba(75, 192, 192, 1)',
                borderWidth: 1,
              },
            ],
          },
          options: chartOptions,
        });
  
        const SuicidalChart = new Chart(SuicidalChartRef.current, {
          type: 'line',
          data: {
            labels: Array.from({ length: 24 }, (_, index) => `${index}:00`),
            datasets: [
              {
                label: 'Suicidal',
                data: [],
                backgroundColor: 'rgba(192, 75, 192, 0.2)',
                borderColor: 'rgba(192, 75, 192, 1)',
                borderWidth: 1,
              },
            ],
          },
          options: chartOptions,
        });
  
        const CyberbullyingChart = new Chart(CyberbullyingChartRef.current, {
          type: 'line',
          data: {
            labels: Array.from({ length: 24 }, (_, index) => `${index}:00`),
            datasets: [
              {
                label: 'Cyberbullying',
                data: [],
                backgroundColor: 'rgba(192, 192, 75, 0.2)',
                borderColor: 'rgba(192, 192, 75, 1)',
                borderWidth: 1,
              },
            ],
          },
          options: chartOptions,
        });
  
        const updateCharts = () => {
          const currentDate = new Date();
          const currentHour = currentDate.getHours();
          const currentMinute = currentDate.getMinutes();
  
          const newDepressiveValue = data.depressive[currentHour] || 0;
          const newSuicidalValue = data.suicide[currentHour] || 0;
          const newCyberbullyingValue = data.cyberbullying[currentHour] || 0;
  
          DepressiveChart.data.datasets[0].data[currentHour] = newDepressiveValue;
          SuicidalChart.data.datasets[0].data[currentHour] = newSuicidalValue;
          CyberbullyingChart.data.datasets[0].data[currentHour] = newCyberbullyingValue;
  
          DepressiveChart.data.labels[currentHour] = `${currentHour}:${currentMinute}`;
          SuicidalChart.data.labels[currentHour] = `${currentHour}:${currentMinute}`;
          CyberbullyingChart.data.labels[currentHour] = `${currentHour}:${currentMinute}`;
  
          DepressiveChart.update();
          SuicidalChart.update();
          CyberbullyingChart.update();
        };
  
        // Update the charts every minute
        const interval = setInterval(updateCharts, 60 * 1000);
  
        return () => {
          clearInterval(interval);
          DepressiveChart.destroy();
          SuicidalChart.destroy();
          CyberbullyingChart.destroy();
        };
      };
  
      fetchData();
    }, [dispatch, user.email]);
    if (!isAuthenticated) {
        navigate('/login');
        return null;
      }
  

  // Additional statistics data
  const internetTimeSpent = '2h 30m';
  const internetTimePercentage = '10%';
  const DepressivePercentage = '50%';
  const SuicidalPercentage = '30%';
  const CyberbullyingPercentage = '20%';

  return (
    <div className="bg-indigo-500 min-h-screen">
      <div className="container mx-auto px-4 py-8">
        <h1 className="text-3xl text-white font-bold mb-8">Statistics</h1>
        {/* Charts */}
        <div className="flex flex-wrap -mx-4">
          {/* Depressive */}
          <div className="w-full sm:w-1/2 lg:w-1/3 px-4 mb-8">
            <div className="bg-white p-4 rounded shadow">
              <canvas ref={DepressiveChartRef} />
            </div>
            <div className="text-white text-center mt-2">
              <h2 className="text-lg font-bold mb-2">Depressive</h2>
              <p className="text-xl">{DepressivePercentage}</p>
            </div>
          </div>

          {/* Suicidal */}
          <div className="w-full sm:w-1/2 lg:w-1/3 px-4 mb-8">
            <div className="bg-white p-4 rounded shadow">
              <canvas ref={SuicidalChartRef} />
            </div>
            <div className="text-white text-center mt-2">
              <h2 className="text-lg font-bold mb-2">Suicidal</h2>
              <p className="text-xl">{SuicidalPercentage}</p>
            </div>
          </div>

          {/* Cyberbullying */}
          <div className="w-full sm:w-1/2 lg:w-1/3 px-4 mb-8">
            <div className="bg-white p-4 rounded shadow">
              <canvas ref={CyberbullyingChartRef} />
            </div>
            <div className="text-white text-center mt-2">
              <h2 className="text-lg font-bold mb-2">Cyberbullying</h2>
              <p className="text-xl">{CyberbullyingPercentage}</p>
            </div>
          </div>
        </div>

        {/* Additional Statistics */}
        <div className="bg-white rounded shadow p-4 mb-8">
          <h2 className="text-lg font-bold mb-4">Additional Statistics</h2>
          <div className="flex justify-center items-center">
            <div className="w-1/3 pr-4">
              <div className="h-8 bg-blue-500 rounded" style={{ width: DepressivePercentage }}></div>
              <div className="h-8 bg-pink-500 rounded mt-2" style={{ width: SuicidalPercentage }}></div>
              <div className="h-8 bg-yellow-500 rounded mt-2" style={{ width: CyberbullyingPercentage }}></div>
            </div>
            <div className="w-2/3">
              <div className="flex justify-between">
                <div className="text-lg font-bold">Depressive</div>
                <div className="text-lg font-bold">{DepressivePercentage}</div>
              </div>
              <div className="flex justify-between mt-2">
                <div className="text-lg font-bold">Suicidal</div>
                <div className="text-lg font-bold">{SuicidalPercentage}</div>
              </div>
              <div className="flex justify-between mt-2">
                <div className="text-lg font-bold">Cyberbullying</div>
                <div className="text-lg font-bold">{CyberbullyingPercentage}</div>
              </div>
            </div>
          </div>
          <div className="mt-4">
            <p>Internet Time Spent: {internetTimeSpent}</p>
            <p>Percentage of Day: {internetTimePercentage}</p>
          </div>
        </div>

        {/* Footer */}
        <footer className="text-center text-white text-sm">
          &copy; 2023 Your Website. All rights reserved.
        </footer>
      </div>
    </div>
  );
};

export default Statistics;
