import React from 'react'

const Item = ({ name, position, content, img }) => {
    return (
        <div>
        <div class="p-8 border rounded-lg transition-colors duration-300 transform border cursor-pointer rounded-xl hover:border-transparent group hover:bg-blue-600 dark:border-gray-700 dark:hover:border-transparent">
                <p class="leading-loose text-gray-500 dark:text-white">
                    “{content}”.
                </p>

                <div class="flex items-center mt-8 -mx-2">
                    <img class="object-cover mx-2 rounded-full w-14 shrink-0 h-14 ring-4 ring-gray-300 dark:ring-gray-700" src={img} alt=""/>

                    <div class="mx-2">
                        <h1 class="font-semibold text-gray-800 dark:text-white">{name} </h1>
                        <span class="text-sm text-gray-500 dark:text-white">{position}</span>
                    </div>
                </div>
            </div>
    </div>
    );
  };
  

