import browse from '../assests/browse.png';
import stats from '../assests/stats.png';
import help from '../assests/help.png';
import media from '../assests/media.png';
import result from '../assests/result.png';
import team from '../assests/team.png';

export const Content1 = () => {
    return (
        <div className="px-4 py-5 mx-auto sm:max-w-xl md:max-w-full lg:max-w-screen-xl md:px-18 lg:px-8 lg:py-8">
      <div className="px-4 py-5 mx-auto sm:max-w-xl md:max-w-full lg:max-w-screen-xl md:px-24 lg:px-8 lg:py-5">
        <div className="grid gap-10 lg:grid-cols-2">
          <div className="flex flex-col justify-center md:pr-8 xl:pr-0 lg:max-w-lg">
            <div className="flex items-center justify-center w-16 h-16 mb-4 rounded-full bg-teal-accent-400">
              <img src={browse} className="text-teal-900 w-15 h-15" viewBox="0 0 24 24"/>
                
            </div>
            <div className="max-w-xl mb-6">
              <h2 className="max-w-lg mb-6 font-sans text-3xl font-bold tracking-tight text-gray-900 sm:text-4xl sm:leading-none">
                Diagnosis is done by <span className="text-indigo-700">smart</span> and <span className="text-indigo-700">accurate</span> AI models
              </h2>
              <p className="text-base text-gray-900 md:text-lg">
                As our thoughts are mainly affected by the media we consume,
                This system is powered by an artificial intelligence that is capable of analyzing and identifying
                negative mental health related content.
              </p>
            </div>
            <div>
              <a
                href="/"
                aria-label=""
                className="inline-flex p-3 rounded-lg bg-indigo-200 items-center font-semibold transition-colors duration-200 text-deep-purple-accent-400 hover:text-deep-purple-800"
              >
                Learn more
                <svg
                  className="inline-block w-3 ml-2"
                  fill="currentColor"
                  viewBox="0 0 12 12"
                >
                  <path d="M9.707,5.293l-5-5A1,1,0,0,0,3.293,1.707L7.586,6,3.293,10.293a1,1,0,1,0,1.414,1.414l5-5A1,1,0,0,0,9.707,5.293Z" />
                </svg>
              </a>
            </div>
          </div>
          <div className="flex items-center justify-center">
            <div className="px-3">
              <img
                className="lg:w-full lg:h-full sm:h-1/2 xl:h-full sm:w-1/2 xl:w-full"
                src={media}
                alt="media"
              />
            </div>
          </div>
        </div>
      </div>
      </div>
    );
  };

export const Content2 = () => {
    return (
      <div>
        <div className="px-4 py-5 mx-auto sm:max-w-xl md:max-w-full lg:max-w-screen-xl md:px-24 lg:px-8 lg:py-5">
          <div className="grid gap-10 lg:grid-cols-2">
            <div className="flex items-center justify-center">
              <div className="px-3">
                <img
                  className="lg:w-full lg:h-full sm:h-1/2 xl:h-full sm:w-1/2 xl:w-full"
                  src={result}
                  alt="report result"
                />
              </div>
            </div>
            <div className="flex flex-col justify-center md:pr-8 xl:pr-0 lg:max-w-lg">
              <div className="flex items-center justify-center w-16 h-16 mb-4 rounded-full bg-teal-accent-400">
                <img src={stats} className="text-teal-900 w-15 h-15" viewBox="0 0 24 24"/>
              </div>
              <div className="max-w-xl mb-6">
                <h2 className="max-w-lg mb-6 font-sans text-3xl font-bold tracking-tight text-gray-900 sm:text-4xl sm:leading-none">
                  Convert AI predictions into <span className='text-indigo-700'>useful</span> and 
                  <span className='text-indigo-700'> secure </span> data
                </h2>
                <p className="text-base text-gray-700 md:text-lg">
                  The AI predictions are converted in real time to statistics about the consumed content
                  to insure that what you're browsing is anonymous. These statistics will be provided in a 
                  weekly report to keep you on track with your mental health.
                </p>
              </div>
              <div>
                <a
                  href="/"
                  aria-label=""
                  className="inline-flex p-3 rounded-lg bg-indigo-200 items-center font-semibold transition-colors duration-200 text-deep-purple-accent-400 hover:text-deep-purple-800"
                >
                  Learn more
                  <svg
                    className="inline-block w-3 ml-2"
                    fill="currentColor"
                    viewBox="0 0 12 12"
                  >
                    <path d="M9.707,5.293l-5-5A1,1,0,0,0,3.293,1.707L7.586,6,3.293,10.293a1,1,0,1,0,1.414,1.414l5-5A1,1,0,0,0,9.707,5.293Z" />
                  </svg>
                </a>
              </div>
            </div>
          </div>
        </div>
      </div>
    );
  };

export const Content3 = () => {
    return (
        <div className="px-4 py-5 mx-auto sm:max-w-xl md:max-w-full lg:max-w-screen-xl md:px-24 lg:px-8 lg:py-5">
      <div className="px-4 py-5 mx-auto sm:max-w-xl md:max-w-full lg:max-w-screen-xl md:px-24 lg:px-8 lg:py-5">
        <div className="grid gap-10 lg:grid-cols-2">
          <div className="flex flex-col justify-center md:pr-8 xl:pr-0 lg:max-w-lg">
            <div className="flex items-center justify-center w-16 h-16 mb-4 rounded-full bg-teal-accent-400">
              <img src={help} className="text-teal-900 w-15 h-15" viewBox="0 0 24 24"/>
            </div>
            <div className="max-w-xl mb-6">
              <h2 className="max-w-lg mb-6 font-sans text-3xl font-bold tracking-tight text-gray-900 sm:text-4xl sm:leading-none">
                Provide various <span className='text-indigo-700'> efficient </span> and
                 <span className='text-indigo-700'> innovative</span> ways of help
              </h2>
              <p className="text-base text-gray-700 md:text-lg">
                This system is powered by another AI that will track your mental health.
                And when necessairy, you will be able to talk to a psychologist online anonymously 
                and speak to people who have same to very similar issue.
              </p>
            </div>
            <div>
              <a
                href="/"
                aria-label=""
                className="inline-flex p-3 rounded-lg bg-indigo-200 items-center font-semibold transition-colors duration-200 text-deep-purple-accent-400 hover:text-deep-purple-800"
              >
                Learn more
                <svg
                  className="inline-block w-3 ml-2"
                  fill="currentColor"
                  viewBox="0 0 12 12"
                >
                  <path d="M9.707,5.293l-5-5A1,1,0,0,0,3.293,1.707L7.586,6,3.293,10.293a1,1,0,1,0,1.414,1.414l5-5A1,1,0,0,0,9.707,5.293Z" />
                </svg>
              </a>
            </div>
          </div>
          <div className="flex items-center justify-center">
            <div className="px-3">
              <img
                className="lg:w-full lg:h-full sm:h-1/2 xl:h-full sm:w-1/2 xl:w-full"
                src={team}
                alt=""
              />
            </div>
          </div>
        </div>
      </div>
    </div>
    );
  };
