import React, { useEffect, useRef, useState } from 'react';
import Chart from 'chart.js/auto';

const DepressiveChart = ({ data }) => {
  const chartRef = useRef(null);
  const [chartData, setChartData] = useState([]);

  useEffect(() => {
    const ctx = chartRef.current.getContext('2d');
    const chart = new Chart(ctx, {
      type: 'line',
      data: {
        labels: [],
        datasets: [
          {
            label: 'Depressive Content',
            data: [],
            backgroundColor: 'rgba(75, 192, 192, 0.2)',
            borderColor: 'rgba(75, 192, 192, 1)',
            borderWidth: 1,
          },
        ],
      },
      options: {
        responsive: true,
        scales: {
          x: {
            display: true,
            title: {
              display: true,
              text: 'Time',
            },
          },
          y: {
            display: true,
            title: {
              display: true,
              text: 'Percentage',
            },
          },
        },
      },
    });

    const updateChart = (value) => {
      setChartData((prevChartData) => [...prevChartData, value]);
      chart.data.labels.push(chart.data.labels.length + 1);
      chart.data.datasets[0].data.push(value);
      chart.update();
    };

    // Update the chart every 1 minute
    const interval = setInterval(() => {
      const value = ((data.depressive * 100) / data.total).toFixed(2);
      updateChart(value);
    }, 60 * 1000);

    return () => {
      clearInterval(interval);
      chart.destroy();
    };
  }, [data]);

  return <canvas ref={chartRef} style={{ width: '900px', height: '550px' }} />;
};

export default DepressiveChart;
