import { 
    LOGIN_SUCCESS,
    LOGIN_FAIL,
    USER_LOADED_SUCCESS,
    USER_LOADED_FAIL,
    AUTH_SUCCESS,
    AUTH_FAIL,
    LOGOUT,
    PASS_RESET_FAIL,
    PASS_RESET_SUCCESS,
    PASS_RESET_CONFIRM_FAIL,
    PASS_RESET_CONFIRM_SUCCESS,
    ACTIVATION_SUCCESS,
    ACTIVATION_FAIL,
    SIGNUP_SUCCESS,
    SIGNUP_FAIL
} from '../../actions/Types';

const initState = {
    access: localStorage.getItem('access'),
    refresh: localStorage.getItem('refresh'),
    isAuthenticated: null,
    user: null
};

export default function(state = initState, action){
    const {type, payload} = action;
    switch(type){
        case AUTH_SUCCESS:
            return {
                ...state,
                isAuthenticated: true
            }

        case AUTH_FAIL:
            return {
                ...state,
                isAuthenticated: false
            }

        case LOGIN_SUCCESS: 
            localStorage.setItem('access', payload.access);
            localStorage.setItem('refresh', payload.refresh);
            return{
                ...state,
                isAuthenticated: true,
                access: payload.access,
                refresh: payload.refresh
            }

        case LOGIN_FAIL:
        case SIGNUP_FAIL:
        case LOGOUT:
            localStorage.removeItem('access');
            localStorage.removeItem('refresh');
            return{
                ...state,
                access: null,
                refresh: null,
                isAuthenticated: false,
                user: null
            }
        case SIGNUP_SUCCESS:
            return{
                ...state,
                isAuthenticated: false
            }            
        case USER_LOADED_SUCCESS:
            return{
                ...state,
                isAuthenticated: true,
                user: payload
            }

        case USER_LOADED_FAIL:
            return{
                ...state,
                user: null
            }
        case PASS_RESET_FAIL:
        case PASS_RESET_SUCCESS:
        case PASS_RESET_CONFIRM_FAIL:
        case PASS_RESET_CONFIRM_SUCCESS:
        case ACTIVATION_SUCCESS:
        case ACTIVATION_FAIL:
            return {
                ...state
            }
        default: return state
    }
}
