import React from 'react';

const MainContent = ({ total, depressive, hours }) => (
    <div className="w-4/5 mx-auto">
        <h2 className="text-2xl font-bold text-gray-900 mb-4">Your statistics for the week:</h2>
    <div className="mb-8 flex justify-between">
      <div className="rounded-md bg-indigo-100 p-4 mx-4">
        <h3 className="text-2xl font-bold text-gray-900">Total time spent on the internet</h3>
        <p className="text-lg font-medium text-gray-700">{hours} hours</p>
      </div>
      <div className="rounded-md bg-indigo-100 p-4 mx-4">
        <h3 className="text-2xl font-bold text-gray-900">Total content browsed</h3>
        <p className="text-lg font-medium text-gray-700">{total}</p>
      </div>
      <div className="rounded-md bg-indigo-100 p-4 mx-4">
        <h3 className="text-2xl font-bold text-gray-900">Percentage depressive content</h3>
        <p className="text-lg font-medium text-gray-700">{depressive} %</p>
      </div>
      <div className="rounded-md bg-indigo-100 p-4 mx-4">
        <h3 className="text-2xl font-bold text-gray-900">Hours spent on depressive content</h3>
        <p className="text-lg font-medium text-gray-700">{((hours*depressive)/100).toFixed(2)} Hours</p>
      </div>
    </div>
    <div className="mb-5 rounded-md mb-8 py-8 mx-auto flex justify-center items-center">
    </div>
  </div>
);

export default MainContent;
