import { useState } from "react"

const Item = ({ title, children }) => {
    const [isOpen, setIsOpen] = useState(false);
    return (
        <div class="border-2 border-gray-100 rounded-lg dark:border-gray-700">
        <button class="flex items-center justify-between w-full p-8" onClick={() => setIsOpen(!isOpen)}>
            <h1 class="font-semibold text-gray-700 dark:text-white">{title}</h1>

            <span class={`text-white bg-blue-500 rounded-full transition-transform duration-300 ${
              isOpen ? 'transform rotate-90' : ''
            }`}>
            <svg xmlns="http://www.w3.org/2000/svg" class="w-6 h-6" fill="none" viewBox="0 0 24 24" stroke="currentColor">
                            <path stroke-linecap="round" stroke-linejoin="round" stroke-width="2" d="M12 6v6m0 0v6m0-6h6m-6 0H6" />
                        </svg>
            </span>
        </button>
        <div className={`${isOpen ? 'visible' : 'hidden'}`}>
        <hr class="border-gray-200 dark:border-gray-700"/>

        <p class="p-8 text-sm text-gray-500 dark:text-gray-300">
            {children}
        </p>
        </div>
    </div>
    );
  };
  
export default function FAQ() {
    return (
      <section class="bg-white dark:bg-gray-900">
    <div class="container max-w-4xl px-6 py-10 mx-auto">
        <h1 class="text-4xl font-semibold text-center text-gray-800 dark:text-white">Frequently asked questions</h1>

        <div class="mt-12 space-y-8">
            <Item title="Who can see what i'm browsing and my data?" children="No one. Literally no one will be able to look at your data even your doctor. Your doctor and even you will only be able to access the report made by the AI which can summerize everything while protecting your privacy."/>

            <Item title="What should i do after i intall Psybot?" children="After you install Psybot you're already done! You can just keep browsing casually and nothing has changed. You will be notified by email once your report is done or just login into your dashboard to check your statistics." />

            <Item title="Do I need to be struggling in order to get Psybot?" children="No it is not necessairy to have a problem in order to track your mental health, but on the contrairy it is very important to prevent the problem before even happening by being self-aware of what you're consuming on the internet and learn how to control it. And that guarantees a happy and stable life."/>

            <Item title="What age group can use Psybot?" children="Everyone can use Psybot, no matter what age or gender as Psybot will provide a lot of help and life improvements to all users. And I'll be working on developing Psybot for kids which will include some specific features to protect your children from any threat."/>
        </div>
    </div>
</section>
    )
  }