import { useState } from "react";
import hero from '../assests/bot.png';
import hero2 from '../assests/hero2-bg.png';
import { connect } from "react-redux";

const Hero = ({ isAuthenticated }) => {
    const [menu, setMenu] = useState(false);
    const getStarted = () => (
      <div className="rounded-md shadow transition hover:-translate-y-1">
        <a
          href="/sign-up"
          className="flex w-full items-center justify-center rounded-md border border-transparent bg-indigo-600 px-8 py-3 text-base font-medium text-white hover:bg-indigo-700 md:py-4 md:px-10 md:text-lg"
        >
        Get started
        </a>
      </div>
    );
    const blank = () => {
      <div></div>
    }
    return (
        <div>
            <section>
            <div className="relative h-[845px] w-full relative py-20 px-6 xl:px-0 lg:flex items-center mx-auto">
                <img className="absolute w-full inset-0 h-full object-cover object-center" src={hero2} alt="we care family"/>
                <div className="py-32 relative z-10 mx-auto container">
                    <div  role="contentinfo"  className="w-full lg:w-1/2 h-full">
                        <div tabIndex="0" className="text-4xl lg:text-6xl font-black mb-8">
                        <span className="text-indigo-700 ">Never</span> 
                        <span className="text-black"> worry about your </span>
                        <span className="text-indigo-700 ">mental health </span> 
                        again.
                        </div>
                        <p tabIndex="0" className="font-sans text-gray-800 font-regular mr-4 mb-8">Keeping in track with our mental health is really important for our well-being, so a project that provides smart monitoring, treatement and privacy is the perfect solution for the present 
                        and future generations who we will reply on to build a better world and acheive success and happiness.</p>
                        {/* add get started button */}
                        <div className="mt-5 sm:mt-8 sm:flex sm:justify-center lg:justify-start pb-10">
                {isAuthenticated ? blank() : getStarted()}
                <div className="mt-3 sm:mt-0 sm:ml-3 transition hover:-translate-y-1">
                  <a
                    href="/about-us"
                    className="flex w-full items-center justify-center rounded-md border border-transparent bg-indigo-100 px-8 py-3 text-base font-medium text-indigo-700 hover:bg-indigo-200 md:py-4 md:px-10 md:text-lg"
                  >
                    Learn more
                  </a>
                </div>
              </div>
                    </div>
                </div>
                <div className="absolute w-[full] h-[90%] right-0 bottom-0 hidden lg:block">
                        <img tabIndex="0" role="img" aria-label="mental health" className="pt-20 mx-auto h-full w-full" src={hero}  alt="mental health"/>
                    </div>
            </div>  
        </section>

            <style>
                {`
            /* Top menu */
            .top-100 {
                animation: slideDown 0.5s ease-in-out;
            }
            @keyframes slideDown {
                0% {
                    top: -50%;
                }
                100% {
                    top: 0;
                }
            }
            * {
                outline: none !important;
                -webkit-tap-highlight-color: rgba(0, 0, 0, 0);
                -webkit-tap-highlight-color: transparent;
            }`}
            </style>
        </div>
    );
};

const mapStateToProps = state => ({
  isAuthenticated: state.auth.isAuthenticated
});

export default connect(mapStateToProps, null)(Hero);
