import React from 'react';
import Navbar from './components/Navbar';

const AboutUs = () => {
  return (
    <div>
    <Navbar/>
    <div className="bg-white p-8 flex justify-center">
      <div className="w-3/4">
        <h1 className="text-4xl text-indigo-700 font-medium mb-4 text-center">About Us</h1>
        <div className="bg-white rounded-lg shadow-md p-8">
          <p className="mb-4 text-lg leading-relaxed text-center">
          At Psybot, we are dedicated to revolutionizing the way we approach mental health diagnosis and treatment. We understand that mental health is a growing concern and traditional methods can be difficult, time-consuming, and expensive. That's why we have developed a platform that uses AI to analyze internet browsing data and make it easier for people to understand and manage their mental health.
          </p>
          <p className="mb-4 text-lg leading-relaxed text-center">
          Our system, Psybot, is a website, browser extension, and mobile app that collects and analyzes internet browsing data using AI and machine learning for natural language processing. Our system identifies depressive, suicidal and mental-illness-related content, and calculates the percentage of this type of content out of the total browsed content. This provides users with an accurate diagnosis, and help them keeping track of their mental health.
          </p>
          <p className="mb-4 text-lg leading-relaxed text-center">
          We believe that early detection and treatment is crucial in preventing and curing mental illnesses, especially avoiding their tragic consequences. That's why we have also included an online support group for users who share similar problems and situations. This allows users to connect with others who understand what they're going through, and provide them with the support and understanding they need.
          </p>
          <p className="mb-4 text-lg leading-relaxed text-center">
          We are committed to respecting our users' privacy. We ensure that the collected data will not be saved in the server, and only the statistics calculated based on the data will be saved to be accessible by both the user and the doctor.
          </p>
          <p className="mb-4 text-lg leading-relaxed text-center">
          Our team is made up of experts in the field of mental health and technology, who are dedicated to providing an easy, accessible, and affordable solution for early detection and treatment of mental illnesses.
          </p>
          <p className="mb-4 text-lg leading-relaxed text-center">
          Thank you for considering Psybot as your mental health companion. We are here to help, and we are committed to making a difference in the mental health of our community.
          </p>
        </div>
      </div>
    </div>
    </div>
  );
}

export default AboutUs;
