import axios from 'axios';
import { 
    LOGIN_SUCCESS,
    LOGIN_FAIL,
    USER_LOADED_SUCCESS,
    USER_LOADED_FAIL,
    AUTH_SUCCESS,
    AUTH_FAIL,
    LOGOUT,
    PASS_RESET_FAIL,
    PASS_RESET_SUCCESS,
    PASS_RESET_CONFIRM_FAIL,
    PASS_RESET_CONFIRM_SUCCESS,
    ACTIVATION_SUCCESS,
    ACTIVATION_FAIL,
    SIGNUP_SUCCESS,
    SIGNUP_FAIL
} from './Types';

const REACT_APP_API_URL = 'http://127.0.0.1:8000';


export const login = (email, password) => async dispatch => {
    const config = {
        headers:{
            'Content-Type': 'application/json'
        }
    };
    const body = JSON.stringify({email, password});

    try {
        const res = await axios.post(`${REACT_APP_API_URL}/auth/jwt/create/`, body, config);
        dispatch({
            type: LOGIN_SUCCESS,
            payload: res.data
        });
        dispatch(load_user());
    }catch (err){
        dispatch({
            type: LOGIN_FAIL
        });
    }
};

export const signup = (name, email, password, re_password) => async dispatch => {
    const config = {
        headers:{
            'Content-Type': 'application/json'
        }
    };

    const body = JSON.stringify({name, email, password, re_password});

    try {
        const res = await axios.post(`${REACT_APP_API_URL}/auth/users/`, body, config);
        dispatch({
            type: SIGNUP_SUCCESS,
            payload: res.data
        });
        dispatch(load_user());
    }catch (err){
        dispatch({
            type: SIGNUP_FAIL
        });
    }
}

export const load_user = () => async dispatch => {
    if (localStorage.getItem('access')){
        const config = {
            headers:{
                'Content-Type': 'application/json',
                'Authorization': `JWT ${localStorage.getItem('access')}`,
                'Accept': 'application/json'
            }
        };

        try {
            const res = await axios.get(`${REACT_APP_API_URL}/auth/users/me/`, config);
            dispatch({
                type: USER_LOADED_SUCCESS,
                payload: res.data
            });
            return res.data
        }catch (err){
            dispatch({
                type: USER_LOADED_FAIL
            });
        }
    }else{
        dispatch({
            type: USER_LOADED_FAIL
        });
    }
};

export const activate = (uid, token) => async dispatch => {
    const config = {
        headers:{
            'Content-Type': 'application/json'
        }
    };

    const body = JSON.stringify({uid, token});

    try {
        await axios.post(`${REACT_APP_API_URL}/auth/users/activation/`, body, config);
        dispatch({
            type: ACTIVATION_SUCCESS
        });
        dispatch(load_user());
    }catch (err){
        dispatch({
            type: ACTIVATION_FAIL
        });
    }
}

export const checkAuth = () => async dispatch => {
    if (localStorage.getItem('access')){
        const config = {
            headers:{
                'Content-Type': 'application/json',
            }
        };

        const body = JSON.stringify({ token: localStorage.getItem('access')});

        try{
            const res = await axios.post(`${REACT_APP_API_URL}/auth/jwt/verify/`, body, config)
            if (res.code !== 'token_not_valid'){
                dispatch({
                    type: AUTH_SUCCESS
                });
            }else{
                dispatch({
                    type: AUTH_FAIL
                });
            }
        }catch (err){
            dispatch({
                type: AUTH_FAIL
            });
        }

    } else{
        dispatch({
            type: AUTH_FAIL
        });
    }
};

export const logout = () => async dispatch => {
    dispatch({
        type: LOGOUT
    })
};

export const reset_pass = (email) => async dispatch => {
    const config = {
        headers:{
            'Content-Type': 'application/json'
        }
    };

    const body = JSON.stringify({ email });

    try{
        await axios.post(`${REACT_APP_API_URL}/auth/users/reset_password/`, body, config);
        dispatch({
            type: PASS_RESET_SUCCESS
        });
    } catch (err) {
        dispatch({
            type: PASS_RESET_FAIL
        });
    }
};

export const reset_pass_confirm = (uid, token, new_password, re_new_password) => async dispatch => {
    const config = {
        headers:{
            'Content-Type': 'application/json'
        }
    };

    const body = JSON.stringify({ uid, token, new_password, re_new_password });

    try{
        await axios.post(`${REACT_APP_API_URL}/auth/users/reset_password_confirm/`, body, config);
        dispatch({
            type: PASS_RESET_CONFIRM_SUCCESS
        });
    } catch (err) {
        console.log(err);
        dispatch({
            type: PASS_RESET_CONFIRM_FAIL
        });
    }
}


export const get_user_data = async (email) => {
    const config = {
        headers:{
            'Authorization': `JWT ${localStorage.getItem('access')}`,
        }
    };

    // const body = JSON.stringify({ email });

    try{
        const res = await axios.get(`${REACT_APP_API_URL}/api/person/${email}/`, config);
        return res.data;
    } catch (err) {
        return err;
    }
};
