import './App.css';
import { BrowserRouter as Router, Route, Routes } from 'react-router-dom';
import Landing from './Landing';
import Login from './Login';
import Register from './Register';
import { Provider } from 'react-redux';
import Store from './components/Store';
import Layout from './components/Layout';
import Home from './Home';
import ResetPass from './components/ResetPass';
import ResetPassConf from './components/ResetPassConf';
import Activate from './components/Activate';
import AboutUs from './AboutUs';
import StatisticsPage from './components/Statistics/Statistics';


function App() {
  return (
    <Provider store={Store}>
      <Router>
        <Layout>
        <Routes>
          <Route exact path="/" element={<Landing />}/>
          <Route exact path="/login" element={<Login />}/>
          <Route exact path="/sign-up" element={<Register />}/>
          <Route exact path="/home" element={<Home />}/>
          <Route exact path="/reset-password" element={<ResetPass />}/>
          <Route exact path="/password/reset/confirm/:uid/:token" element={<ResetPassConf />}/>
          <Route exact path="/active/:uid/:token" element={<Activate />}/>
          <Route exact path="/about-us" element={<AboutUs />}/>
          <Route exact path="/statistics" element={<StatisticsPage />}/>
        </Routes>
        </Layout>
      </Router>
    </Provider>
  );
}

export default App;
