import React from 'react'
import { useState } from "react";
import Psybot from '../assests/Psybot.png';
import { connect } from 'react-redux';
import { logout } from '../actions/auth';

function Navbar({ isAuthenticated, logout }) {
    const [menu, setMenu] = useState(false);
    const blank = () => {
        <div></div>
      }

    const guestLinks = () => (
        <a href='/login'>
            <div className="px-10">                            
                <button role="button" aria-label="sign in" className="focus:bg-indigo-600 focus:ring-2 focus:ring-offset-2 focus:ring-indigo-700 bg-indigo-700 hover:bg-indigo-600 text-white px-6 py-2 font-medium rounded focus:outline-none">Login</button>
            </div>
        </a>
    );

    const authLinks = () => (
        <a href='/home'>
            <div className="px-10">                            
                <button role="button" aria-label="home" className="focus:bg-indigo-600 focus:ring-2 focus:ring-offset-2 focus:ring-indigo-700 bg-indigo-700 hover:bg-indigo-600 text-white px-6 py-2 font-medium rounded focus:outline-none">Home</button>
            </div>
        </a>
    );

    const Logout = () => (
        <li>
            <a onClick={logout} className="hover:text-indigo-500 text-lg focus:text-indigo-500 font-medium" href="/">Logout</a>
        </li>
    );

    return (
        <div>
                    <nav className="lg:hidden relative z-40 sticky top-0">
                        <div className="flex py-6 justify-between items-center px-4">
                            <div>
                                <img src={Psybot} alt="logo" />
                            </div> 
                            <div className="flex items-center">
                                <div className="xl:hidden">
                                <img id="open" className={` ${menu ? 'hidden' : '' } close-m-menu`} onClick={() => setMenu(!menu)}  src="https://tuk-cdn.s3.amazonaws.com/can-uploader/right_aligned_with_searchbar_Svg1.svg" alt="menu" />
                                    <div id="close" className={` ${menu ? '' : 'hidden' } close-m-menu`} onClick={() => setMenu(!menu)}>
                                        <img src="https://tuk-cdn.s3.amazonaws.com/can-uploader/right_aligned_with_searchbar_Svg2.svg" alt="cross" />
                                    </div>
                                </div>
                            </div>
                        </div>
                    </nav>
                    <nav  role="navigation" aria-label="Main" tabIndex="0" className="hidden relative z-40 w-full lg:flex justify-between items-center px-20 py-4 sticky top-0 bg-[#F2FAFF] ">
                        <div className="w-1/6">
                            <a tabIndex="0" aria-label="Psybot logo" href="/">
                                <img src={Psybot} alt="logo" className='h-[66px]'/>
                            </a>
                        </div>
                        <div className="w-5/6">
                            <div className="flex items-center justify-end">
                                <ul className="text-gray-800 lg:space-x-8 flex items-center leading-none">
                                    <li>
                                        <a className="hover:text-indigo-500 text-lg focus:text-indigo-500 font-medium" href="/about-us">About Us</a>
                                    </li>
                                    <li className="ml-4 hover:text-indigo-500 ">
                                        <a className="focus:text-indigo-500 text-lg font-medium" href="#">Blog</a>
                                    </li>
                                    <li className="ml-4 hover:text-indigo-500 focus:text-indigo-500">
                                        <a className="focus:text-indigo-500 text-lg font-medium" href="#">Services</a>
                                    </li>
                                    <li className="ml-4 hover:text-indigo-500 focus:text-indigo-500">
                                        <a className="focus:text-indigo-500 text-lg font-medium"  href="#">Support</a>
                                    </li>
                                    { isAuthenticated ? Logout() :  blank()}
                                </ul>
                                { isAuthenticated ? authLinks() : guestLinks() }
                            </div>
                        </div>
                    </nav>
        </div>
    );
    }

const mapStateToProps = state => ({
    isAuthenticated: state.auth.isAuthenticated
});

export default connect(mapStateToProps, { logout })(Navbar);
