import React, { useEffect, useState } from 'react';
import { useNavigate } from 'react-router-dom';
import { connect } from 'react-redux';
import Dash from './components/Dash/Dash';
import { get_user_data, load_user } from './actions/auth';
import DepressiveChart from './components/Dash/Chart';

const Home = ({ isAuthenticated, load_user }) => {
  const navigate = useNavigate();
  const [name, setName] = useState('');
  const [data, setData] = useState(null);

  useEffect(() => {
    const fetchData = async () => {
      const user = await load_user();
      setName(user.name);
      const data = await get_user_data(user.email);
      setData(data);
    }
    fetchData();
  }, []);
  if (!isAuthenticated) {
    return navigate('/login');
  }
  
  return (
    <div>
        <Dash data={data} name={name}/>
    </div>
  );
};

const mapStateToProps = (state) => ({
  isAuthenticated: state.auth.isAuthenticated,
});

export default connect(mapStateToProps, { get_user_data, load_user })(Home);
